*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.App {
  width: 100%;
  min-height: 100vh;
  background: rgb(11, 34, 26);
  display: flex;
}

img{
  display: block;
  cursor: pointer;
}

.container {
  margin: auto;
  width: 100%;
  max-width: 540px;
}

.container .selected {
  width: 300px;
  height: 300px;
  margin: 0 auto;
  border: 4px solid rgb(3, 170, 170);
}

.container .imgContainer { 
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 40px 0 0 0;
}

.container .imgContainer img {
  width: 100px;
  height: 100px;
  margin: 5px 0;
}